import React from "react";
import { Link } from "react-router-dom";
import { useState } from "react";
import Carousell from "./Carousel";
import "./Home.css";
import Button from "../Button";
import img1 from "../../icons/1.png";
import img2 from "../../icons/2.png";
import img3 from "../../icons/3.png";
import img4 from "../../icons/4.png";
import img5 from "../../icons/5.png";
import img6 from "../../icons/6.png";
import heroImage from "../../images/homepage.png";
import about from "../../images/DT-image.png";
import valuation from "../../images/valuation.png";
import architecture from "../../images/architecture.png";

const servicesarr = [
  {
    id: 0,
    discription: (
      <ul>
        <li>Cost estimate of residental building</li>
        <li>Cost estimate of commercial building</li>
        <li>Cost estimate of complex</li>
      </ul>
    ),
    title: "Estimate & Valuation",
    imageTop: img1,
    imageDown: valuation,
  },
  {
    id: 1,
    discription: "",
    title: "3D Design",
    imageTop: img2,
    imageDown: img2,
  },
  {
    id: 2,
    discription: "",
    title: "Structure Analysis",
    imageTop: img3,
    imageDown: img3,
  },
  // {
  //   id: 3,
  //   discription: "This is description 4",
  //   title: "This is title 4",
  //   image: img4,
  // },
  {
    id: 4,
    discription: "",
    title: "Architectural Design",
    imageTop: img5,
    imageDown: architecture,
  },
  {
    id: 5,
    discription: "",
    title: "Survey",
    imageTop: img6,
    imageDown: img6,
  },
];

function Home() {
  const [discription, setDiscription] = useState("");
  const [heading, setHeading] = useState("");
  const [image, setImage] = useState();

  const handleClick1 = (disc) => setDiscription(disc);
  const handleClick2 = (heading) => setHeading(heading);
  const handleClick3 = (img) => setImage(img);

  return (
    <div>
      <div className="heroImage">
        <img src={heroImage} alt="HEROIMAGE" />
      </div>
      <div style={{ background: "#b5d4d9" }}>
        <div className="about container">
          <div className="aboutParagraph" style={{ maxWidth: "90%" }}>
            <h2 className="aboutHeading">Design Asociate Nepal</h2>
            <br />
            <p>
              Design Associates Nepal, A noble startup company is a group of
              like minded individuals striving to deliver different services to
              our valued clients using the best of our abilities with the only
              motive of client satisfaction at top.
            </p>
            <Link to="/About">
              <Button Name={"Read More"} />
            </Link>
          </div>
          <div className="aboutImg">
            <img src={about} alt="About" />
          </div>
        </div>
      </div>

      <div className="services">
        <h1 className="container">Our Specilization</h1>
        <div className="service">
          {servicesarr.map((item, index) => {
            return (
              <div
                onClick={() => {
                  handleClick1(item.discription);
                  handleClick2(item.title);
                  handleClick3(item.imageDown);
                }}
              >
                {/* {item.title} */}

                <div>
                  <img src={item.imageTop} />
                </div>
              </div>
            );
          })}
        </div>
      </div>
      <div className="servicesInfo container">
        <div style={{ width: "90%" }}>
          <h2 className="serviceHeading">{heading}</h2>
          <p className="serviceParagraph">{discription}</p>
        </div>
        <div>
          <img src={image} alt="" />
        </div>
      </div>

      {/* <div className="projectCompleted" style={{ background: "#b5d4d9" }}>
        <h1>Project Completed </h1>
        <h1 className="ProjectNumber">275</h1>
      </div> */}
      <div className="carousel">
        <Carousell />
      </div>
    </div> /*  maindiv */
  );
}

export default Home;
